.header {
  display: flex;
  justify-content: space-between;
  margin: 10px;
  border: 1px solid black;

}

.nav-items > ul{
  list-style-type: none;
  display: flex; 
}

.nav-items > ul > li {
  padding : 10px;
}

.logo {
  width: 100px;
}

.card  {
  width : 200px;
  border : 1px solid black;
  padding: 10px;
  margin: 10px;
}

.card > img {
  width : 100%;
}


.restaurant-list {
  display: flex;
  flex-wrap: wrap;
}


.shimmer-card {
  width: 200px;
  height: 280px;
  background-color: lightgray;
  margin: 10px;
}